import React from 'react';
import {Container} from '@mui/material'
import useWindowDimensions from "../../dimensions.js";
import Details from './Details';
import Email from './Email';

export default function Contact() {

    const { width, height } = useWindowDimensions();

    let avHeight = window.screen.height;
    let avWidth = window.screen.width;

  return (
    <Container maxWidth='xl' className='contact' style={{display: 'flex', flexDirection: avWidth >= avHeight && avHeight > 600 ? 'row' : 'column', flex: 1, minHeight: height - 56}}>
        <Details/>
        <Email/>
    </Container>
  )
}
